<template>
  <!--  -->
  <div class="footer">
    <v-row class="footer_con">
      <v-col cols="9">
        <div class="logo_con">
          <img
            src="../../assets/logo/scrollblogo.svg"
            alt=""
            style="cursor: pointer; width: 150px; height: 36px"
          />
        </div>
        <div class="footer_info">
          <p>  684 5F</p>
          <p>
            TEL 051-301-5908 | FAX 3030-3448-0523 | E-mail:
            gameinscokr@naver.com
          </p>
          <p>
            () | :  | : 362-88-00435 |
             : 1649-3196-3663-0809
          </p>
          <br /><br />
          <p>copyright 2022 GAMEINS All right reserved</p>
        </div>
      </v-col>
      <v-spacer />
      <v-col cols="3">
        <div class="contact_btn">
          <div v-for="(btnText, index) in btn" :key="index">
            <div class="btn">
              <a :href="btnText.link">
                {{ btnText.text }}
                <img :src="btnText.logo_image" />
              </a>
            </div>
          </div>
        </div>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import store from "@/store";
//   import { onMounted, reactive } from "vue";
import { mapGetters } from "vuex";

export default {
  name: "Footer",
  data() {
    return {
      btn: [
        {
          link: "https://blog.naver.com/gameinscokr",
          text: "BLOG",
          logo_image: require("@/assets/footer/blog.svg"),
        },
        {
          link: "https://www.instagram.com/tikintaka_official",
          text: "INSTAGRAM",
          logo_image: require("@/assets/footer/insta.svg"),
        },
        {
          link: "/contact",
          text: "& ",
          logo_image: require("@/assets/footer/message.svg"),
        },
        {
          link: "/news",
          text: "",
          logo_image: require("@/assets/footer/certification.svg"),
        },
        {
          link: "/",
          text: "",
          logo_image: require("@/assets/footer/personal.svg"),
        },
        {
          link: "/Intro",
          text: "",
          logo_image: require("@/assets/footer/locate.svg"),
        },
      ],
    };
  },
  computed: {
    ...mapGetters(["isScrolling"]),
  },
  mounted() {
    const footer_con = document.querySelector(".footer");

    footer_con.addEventListener("wheel", function (e) {
      console.log("footer-wheel");
      e.preventDefault();
      if (store.getters.isScrolling) {
        return;
      }
      store.dispatch("setIsScrolling", true);

      if (e.deltaY < 0) {
        window.scrollTo({
          top: window.scrollY - footer_con.scrollHeight,
          behavior: "smooth",
        });
      }

      setTimeout(() => {
        store.dispatch("setIsScrolling", false);
      }, 500);
    });
  },
};
</script>

<style lang="scss" scoped>
/*  */

.footer {
  position: relative;
  width: 100vw;
  height: auto;
  background-color: #242424;
  box-shadow: 0px 0px 1px 0px grey;
}

.footer_con {
  width: 68vw;
  display: flex;
  margin: auto;
}

.logo_con {
  padding-top: 12px;
  margin-bottom: 8%;
}
.footer_info {
  color: #ffffff;
  font-family: "Pretendard-Regular";
  font-size: 0.9rem;
  margin-top: 12px;
}
.contact_btn {
  display: grid;
  width: 100%;
  padding-top: 12px;
  justify-content: right;
}
.btn {
  font-size: 0.9rem;
  width: 180px;
  height: 36px;
  box-shadow: 0px 0px 1px 0px #847a7a;
  display: flex;
  align-items: center;
  padding: 12px;
  margin: 3px;
}
a {
  text-decoration: none;
  color: #b3b3b3;
  width: 100%;
  justify-content: space-between;
  display: flex;
}
</style>
