<template>
  <!--  section -->
  <section class="contact_section">
    <!-- <div class="contact_bg" /> -->
    <div class="contact_con">
      <div class="box">
        <span class="title"
          >     .</span
        >
        <span class="desc">   ?</span>
        <router-link to="/Contact">
          <img class="btn" src="./../../assets/home/togo_icon.svg" />
        </router-link>
      </div>
    </div>
    <Footer />
  </section>
</template>

<script>
import Footer from "./../footer/Footer_Home.vue";
import { mapGetters } from "vuex";
import store from "@/store";

export default {
  name: "contact_section",
  components: {
    Footer,
  },
  computed: {
    ...mapGetters(["isScrolling"]),
  },
  data() {
    return {};
  },
  mounted() {
    const contact_con = document.querySelector(".contact_con");
    const contact_section = document.querySelector(".contact_section");

    var max_Scroll = document.documentElement.scrollHeight - window.innerHeight;

    contact_con.addEventListener("wheel", function (e) {
      e.preventDefault();
      if (store.getters.isScrolling) {
        return;
      }

      store.dispatch("setIsScrolling", true);
      if (e.deltaY < 0) {
        if (window.scrollY === max_Scroll) {
          window.scrollTo({
            top: window.scrollY - contact_section.scrollHeight,
            behavior: "smooth",
          });
        } else {
          window.scrollTo({
            top: window.scrollY - contact_con.scrollHeight,
            behavior: "smooth",
          });
        }
      } else if (e.deltaY > 0) {
        window.scrollTo({
          top: window.scrollY + contact_con.scrollHeight,
          behavior: "smooth",
        });
      }
      setTimeout(() => {
        store.dispatch("setIsScrolling", false);
      }, 500);
    });
  },
};
</script>

<style lang="scss" scoped>
.contact_section {
  position: relative;
  width: 100vw;
  height: 40vh;
}

.contact_bg {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: -1;
}
.contact_con {
  background-size: cover;
  background-image: url("./../../assets/home/contact_img.svg");
  height: 100%;
  .box {
    width: 30%;
    display: grid;
    height: 100%;
    margin: 0% 17%;
    color: white;
  }
}

.title {
  font-family: "Pretendard-Regular";
  font-weight: bold;
  font-size: 2.5rem;
  padding-top: 13%;
}

.desc {
  font-family: "Pretendard-Regular";
  font-size: 1.25rem;
  margin-bottom: 2%;
}

.btn {
  cursor: pointer;
  padding-bottom: 5%;
  width: 5.25rem;
}
</style>
