<template>
  <section class="service_section">
    <div class="service_bg">
      <v-row>
        <div class="service_text">
          <span class="service_sub_title">SERVICE</span>
          <span class="service_sub_subtitle"
            >   .</span
          >
          <span class="service_sub_detail">
            <p>
                /  ,    
               
            </p>
                ,  
             .
          </span>
        </div>
      </v-row>
      <v-row>
        <div class="card_wrap">
          <div class="webapp_img">
            <div class="hover_img"></div>
            <div class="card_text">
              <p class="card_title">Web / APP</p>
              <p class="card_title_2">Development</p>
              <p class="card_desc">Web/App A to Z </p>
            </div>
            <router-link to="/service">
              <div class="web_logo_btn">
                <span class="web-line1"></span>
                <span class="web-line2"></span>
              </div>
            </router-link>
          </div>
          <div class="platform_img">
            <div class="hover_img">
              <!-- <div class="card_text">
                <p class="card_title">SERVICE</p>
                <p class="card_title_2">PLATFORM</p>
                <p class="card_desc"> A to Z </p>
              </div> -->
            </div>
            <div class="card_text_hover">
              <p class="card_title">SERVICE</p>
              <p class="card_title_2">PLATFORM</p>
              <p class="card_desc"> A to Z </p>
            </div>
            <router-link to="/service">
              <div class="platform_logo_btn">
                <span class="platform-line1"></span>
                <span class="platform-line2"></span>
              </div>
            </router-link>
          </div>
        </div>
      </v-row>
    </div>
  </section>
  <ScrollNav />
</template>

<script>
import ScrollNav from "../header/HeaderNav.vue";
import { mapGetters } from "vuex";
import store from "@/store";

export default {
  components: {
    ScrollNav,
  },
  computed: {
    ...mapGetters(["isScrolling"]),
  },
  mounted() {
    const section = document.querySelector(".service_section");

    section.addEventListener("wheel", function (e) {
      e.preventDefault();
      if (store.getters.isScrolling) {
        return;
      }
      store.dispatch("setIsScrolling", true);
      if (e.deltaY < 0) {
        window.scrollTo({
          top: window.scrollY - window.innerHeight,
          behavior: "smooth",
        });
      } else if (e.deltaY > 0) {
        window.scrollTo({
          top: window.scrollY + window.innerHeight,
          behavior: "smooth",
        });
      }
      setTimeout(() => {
        store.dispatch("setIsScrolling", false); //   isScrolling  false 
      }, 500); // 1   });
    });

    // const text = document.querySelectorAll(".card_text");
    // const text_hover = document.querySelectorAll(".card_text_hover");
    const webapp = document.querySelector(".webapp_img");
    const platform = document.querySelector(".platform_img");
    const text = document.querySelector(".card_text");
    const text2 = document.querySelector(".card_text_hover");
    const web_btn = document.querySelector(".web_logo_btn");
    const platform_btn = document.querySelector(".platform_logo_btn");
    const web_line_1 = document.querySelector(".web-line1");
    const web_line_2 = document.querySelector(".web-line2");
    const platform_line_1 = document.querySelector(".platform-line1");
    const platform_line_2 = document.querySelector(".platform-line2");

    webapp.addEventListener("mouseover", () => {
      text2.style.opacity = 0;
      platform_btn.style.opacity = 0;
      web_btn.style.background = "white";
      web_line_1.style.backgroundColor = "#4187fe";
      web_line_2.style.backgroundColor = "#4187fe";
    });
    webapp.addEventListener("mouseleave", () => {
      setTimeout(() => {
        text2.style.opacity = 1;
        platform_btn.style.opacity = 1;
      }, 1000);
      web_line_1.style.backgroundColor = "white";
      web_line_2.style.backgroundColor = "white";
      web_btn.style.background = "none";
    });
    platform.addEventListener("mouseover", () => {
      text.style.opacity = 0;
      web_btn.style.opacity = 0;
      platform_btn.style.background = "white";
      platform_line_1.style.backgroundColor = "#4187fe";
      platform_line_2.style.backgroundColor = "#4187fe";
    });
    platform.addEventListener("mouseleave", () => {
      setTimeout(() => {
        text.style.opacity = 1;
        web_btn.style.opacity = 1;
      }, 1000);
      platform_line_1.style.backgroundColor = "white";
      platform_line_2.style.backgroundColor = "white";
      platform_btn.style.background = "none";
    });
  },
};
</script>

<style lang="scss" scoped>
.service_section {
  width: 100vw;
  height: 100vh;
  margin-top: 0.65%;
}

.card_wrap {
  display: flex;
  bottom: 0;
  position: absolute;
}

.webapp_img {
  width: 33vw;
  height: 59vh;
  position: relative;

  .hover_img {
    position: relative;
    width: 100%;
    height: 100%;
    z-index: 10;
    left: 0;
    overflow: hidden;
    transition: all 1s ease-in-out;

    background-size: cover;
    background-image: url(./../../assets/home/service_big_1.svg);
    // background-attachment: fixed;

    .logo_btn {
      cursor: pointer;
    }
  }

  .card_text {
    bottom: 0;
    position: absolute;
    padding: 0% 0px 5% 10%;
    z-index: 14;
    transition: all 0.5s ease;
  }

  .hover_img:hover {
    width: 100vw;
    z-index: 14;
    height: 100%;
    position: absolute;
    left: -50%;
    background-size: cover;
  }
}

.platform_img {
  width: 33vw;
  height: 59vh;
  position: relative;

  .hover_img {
    position: relative;
    width: 100%;
    height: 100%;
    z-index: 10;
    left: 0;
    overflow: hidden;
    transition-property: width, left;
    transition: 1s ease-in-out;
    background-size: cover;
    background-image: url("./../../assets/home/service_big_2.svg");
    // background-attachment: fixed;
  }
  .card_text_hover {
    bottom: 0;
    position: absolute;
    padding: 0% 0px 5% 10%;
    z-index: 14;
    transition: all 0.5s ease;
  }

  .hover_img:hover {
    opacity: 1;
    width: 100vw;
    z-index: 14;
    height: 100%;
    position: absolute;
    left: -150%;
    background-size: cover;
  }
}

.card_title {
  font-family: sans-serif;
  font-size: 2rem;
  font-weight: bold;
  line-height: 26px;
}

.card_title_2 {
  font-family: sans-serif;
  font-weight: bold;
  font-size: 2rem;
}

.card_desc {
  font-family: "Pretendard-Regular";
  font-size: 1.125rem;
  padding-top: 12px;
}

.service_bg {
  position: relative;
  width: 100vw;
  height: 100vh;
  padding: 0% 17%;
  background: rgb(43, 30, 21);
  color: white;
}

.service_text {
  display: grid;
  position: relative;
  margin-top: 18vh;

  .service_sub_title {
    font-family: sans-serif;
    color: white;
    font-size: 1.25rem;
  }

  .service_sub_subtitle {
    font-family: "Pretendard-Regular";
    font-size: 1.825rem;
    padding: 16px;
  }

  .service_sub_detail {
    font-family: "Pretendard-Regular";
    font-size: 1.125rem;
    padding-left: 16px;
  }
}

.web_logo_btn,
.platform_logo_btn {
  position: absolute;
  width: 60px;
  height: 60px;
  z-index: 15;
  border-radius: 100%;
  text-align: center;
  box-shadow: 0px 0px 0px 2px;
  margin: 10% 10%;
  bottom: 0;
  right: 0;
  transition: all 1s ease;

  .web-line1,
  .platform-line1 {
    width: 37px;
    height: 2.1px;
    display: inline-block;
    border-radius: 12%;
    background-color: white;
    transform: translate3d(10px, 17.5px, 10px);
    transition: all 1s ease;
  }

  .web-line2,
  .platform-line2 {
    left: -14.6%;
    bottom: -15.5%;
    width: 21px;
    height: 2.1px;
    display: inline-block;
    border-radius: 12%;
    position: relative;
    background-color: white;
    transform: rotate3d(0, 0, -1, -45deg);
    transition: all 1s ease;
  }
}

.service_bg {
  background-image: url("./../../assets/home/service_bg.svg");
  background-size: cover;
}

.webapp {
  background-image: url("./../../assets/home/service1.svg");
}

.platform {
  background-image: url("./../../assets/home/service2.svg");
}

a {
  text-decoration: none;
  color: white;
}
</style>
